import { Box, Grid, Heading, Flex, Text, Image, Tag, TagLabel, Button, Icon, position } from '@chakra-ui/react'
import { BsGithub, BsLink45Deg } from 'react-icons/bs';


const style = {
    lvl0: {
        p: '15px',
        boxShadow:'lg',
        bg:'rgb(245,245,245)',
    },
    lvl1: {
        align:'center'
    },
    imgBox: {
        w: ['60%'],
        ml:'30px',
        boxShadow:'md',
        h:'fit-content'
    },
    img: {
        w: ['100%']
    },
    descBox: {
        direction: 'column',
        pl: '35px'
    },
    title: {
        mb: '15px',
        p: '5px 15px',
        fontSize: '42px',
        fontWeight: '700'


    },
    desc: {
        mb: '15px',
        p: '5px 15px'

    },
    genStack: {
        wrap: 'wrap',
        mb: '10px'

    },
    lagStack: {

    },
    toolStack: {

    },
    gitButton: {
        bg: '#fff',
        boxShadow: '0px 0px 10px 2px grey',
        mr: '20px'
    },
    netButton: {
        bg: '#05a6f3',
        boxShadow: '0px 0px 10px 2px grey',

    }
}

export const ProjectCard = ({ data, id }) => {
    const i = data;
    return <Box {...style.lvl0} >
        <Flex {...style.lvl1}>

            <Box {...style.imgBox}>
                <Image {...style.img} src={i.image[0]} />
            </Box>

            <Flex {...style.descBox}>

                <Text {...style.title}>{i.title}</Text>

                <Flex {...style.genStack}>
                    {i.genStack.map((j, f) => <Tag key={f + id + 'genstack'} mr='15px' mb='15px' size='lg' bg='#05a6f3' color='#fff' borderRadius='10px'>
                        <TagLabel ml='15px'>{j}</TagLabel>
                    </Tag>
                    )}
                </Flex>

                <Text {...style.desc}>{i.desc}</Text>

                <Flex {...style.genStack}>
                    {i.lagStack.map((j, f) => <Tag key={f + id + 'lagstack'} mr='15px' mb='15px' size='lg' bg='grey' color='#fff' borderRadius='10px'>
                        <TagLabel ml='15px'>{j.t}</TagLabel>
                        <Image w='25px' ml='15px' h='25px' src={j.i} />
                    </Tag>
                    )}
                </Flex>

                <Flex   {...style.genStack}>
                    {i.toolStack.map((j, f) => <Tag key={f + id + 'toolstack'} mr='15px' mb='15px' size='sm' bg='#999' color='#fff' borderRadius='10px'>
                        <TagLabel ml='15px'>{j.t}</TagLabel>
                        <Image w='25px' h='25px' ml='15px' src={j.i} />
                    </Tag>
                    )}
                </Flex>

            </Flex>
        </Flex>
        <Flex w={['450px']} justify='space-evenly' align={'center'} m='30px auto'>
            <Button {...style.gitButton}> <a href={i.github} target='_blank'><Icon as={BsGithub} mr='7px' />{'| Github'}</a></Button>
            <Button {...style.netButton}> <a href={i.deploy} target='_blank'> <Icon as={BsLink45Deg} mr='7px' />| Deployment </a></Button>
        </Flex>
    </Box>
}